import React, {useState} from 'react';



export const Progress = () => {
    const [current, setcurrent] = useState(0);

    return (
        <div>
            {current}%
            <div>
                
                <button onClick={() => setcurrent(current > 0 ?current - 5 : 0)}>-</button>
                <button onClick={() => setcurrent(current < 100 ? current + 5: 100)}>+</button>
            </div>
        </div>
    );
};
export default Progress